@import '../fonts/iconfont.css';
body {
  overflow-x: hidden;
  height: 100%;
}
.header {
  position: fixed;
  z-index: 99;
  top: 0;
  width: 100%;
}
.header .title {
  display: inline;
  color: #4fa1de;
  cursor: pointer;
}
.header .title .large-title {
  font-size: 36px;
  font-weight: 700;
  line-height: 53px;
}
.header .title .normal-title {
  margin-left: 3px;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}
.header .login-info {
  float: right;
  color: black;
}
.header .login-info .logout {
  margin-left: 20px;
  display: inline-block;
  cursor: pointer;
}
.content {
  position: relative;
  margin-top: 64px;
}
.content .under-border {
  border-bottom: 1px solid #dcdcdc;
}
.online-user-pop::-webkit-scrollbar {
  width: 8px;
}
.online-user-pop::-webkit-scrollbar-button {
  display: none;
}
.online-user-pop::-webkit-scrollbar-track {
  border-radius: 4px;
  background-color: #0e5897;
}
.online-user-pop::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: lightgray;
}



